<template>
      <v-bottom-sheet v-model="sheet" inset>
          <template v-slot:activator="{ on, attrs }">
                <v-sheet v-bind="attrs" v-on="on" class="d-flex justify-space-around align-center select" height="100%">
                    <p class="text-center">Buscar disponibilidad</p>
                    <v-btn class="mx-2" fab dark color="indigo">
                        <v-icon dark>mdi-magnify</v-icon>
                    </v-btn>
                </v-sheet>

          </template>

          <v-sheet :height="height" class="pa-5">
            <v-container fluid>
                <v-row no-gutters>
                    <v-col cols="12">
                      <h2 class="primary--text">Buscar disponibilidad de equipo</h2>
                      <br>
                      <v-text-field
                                label="Buscar equipo"
                                required
                                v-model="value"
                      ></v-text-field>
                      <v-btn color="primary" link @click="onsubmit">
                          Buscar
                      </v-btn>
                    </v-col>

                    <v-col cols="12">
                      <v-sheet height="400px" class="">
                        <v-row>
                          <v-col cols="4" v-for="(item,i) in items.equipos" :key="i">
                              <v-card outlined>
                                  <v-list-item three-line>
                                    <v-list-item-content>
                                      <div class="overline mb-4">
   {{ items.ocupados.filter( i => i == item.id).length != 0 ? "Ocupado" : "Libre" }}
                                      </div>
                                      <v-list-item-title class="headline mb-1">
                                        {{item.nombre}}
                                      </v-list-item-title>
                                      <v-list-item-subtitle>
                                        {{item.marca}}
                                      </v-list-item-subtitle>
                                    </v-list-item-content>
                                    <v-list-item-avatar
                                      tile
                                      size="100"
                                      color="grey"
                                      
                                    >
                                    <img :src="item.foto">

                                    </v-list-item-avatar>
                                  </v-list-item>
                              </v-card>
                          </v-col>
  
                        </v-row>
                      </v-sheet>
                    </v-col>

                    <v-col cols="12"> 
                      <v-btn @click="sheet=!sheet; height=300;items={};value=''">Salir</v-btn>
                    </v-col> 

                         
                  </v-row>
            </v-container>
          </v-sheet>
       </v-bottom-sheet>
</template>
<script>
import api from '@/store/api.js'


export default {
  name: 'searchequipment',
  data: () => ({
    sheet: false,
    value: '',
    height: 300,
    items: {
      equipos: [],
      ocupados: []
    }
    
  }),
  methods: {
    async onsubmit(){
      let response = await api.search({
        value: this.value
      });
      this.height = 700
      this.items = response.data

    }  
  }
}
</script>