<template>
      <v-bottom-sheet v-model="sheet" inset>
          <template v-slot:activator="{ on, attrs }">
                <v-sheet v-bind="attrs" v-on="on" class="select d-flex justify-space-around align-center" height="100%">
                                <p class="text-center">Solicitar equipo</p>
                                <v-btn class="mx-2" fab dark color="indigo">
                                    <v-icon dark>mdi-card-text</v-icon>
                                </v-btn>
                </v-sheet>
          </template>

          <v-sheet height="500px" class="pa-5">
            <v-container fluid>
                <v-row no-gutters>
                    <v-col cols="12">
                      <h2 class="primary--text">Solicitar equipo</h2>
                      <br>
                    </v-col>

                    <v-col cols="12">
                      <v-row>
                        <v-col cols="5">
                            <v-select v-model="selected" dense :items="departamentos" label="Selecciona un departamento"></v-select>

                               <v-dialog
                                  v-model="dialog"
                                  persistent
                                  max-width="500"
                                
                                >
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-btn v-bind="attrs" v-on="on" @click="onsubmit">Generar nuevo cdigo</v-btn>
                                  </template>
                                  <v-card>
                                    <v-card-title class="headline">
                                      T nuevo cdigo: {{ codigo.id }}
                                    </v-card-title>
                                    <v-card-text>
                                      Presenta este cdigo en el departamentos de recursos para poder usar el equipo
                                      que esta bajo tu resguardo.
                                    </v-card-text>
                                    <v-card-actions>
                                      <v-spacer></v-spacer>
                                
                                            <v-btn
                                              color="green darken-1"
                                              text
                                              @click="dialog = false"
                                            >
                                              Aceptar
                                            </v-btn>
                                    </v-card-actions>
                                  </v-card>
                                </v-dialog>
                        </v-col>

                        <v-col cols="7">
                            <div class="pl-3 text-body-1 font-weight-bold">Mis cdigos</div>
                          
                              <v-data-table
                              :headers="headers"
                              :items="getitems('salidas')"
                              :items-per-page="5"
                              
                              ></v-data-table>

                        </v-col>
                      </v-row>
                    </v-col>    

                         
                  </v-row>
            </v-container>
          </v-sheet>
       </v-bottom-sheet>
</template>

<script>
import { mapGetters, mapActions  } from 'vuex'

export default {
  name: 'requestequipment',
  created (){

    this.salidas = this.getitems('salidas')

    this.getuser.departamentos.forEach(element => {

      this.departamentos.push(element.nombre)
      
    });


  },
  data: () => ({
    sheet: false,
    dialog: false,
    selected: '',
    salidas: [],
    departamentos: [],
    codigo: {},
    headers: [
          { text: 'codigo',value: 'id'},
          { text: 'fecha',value: 'fecha'},
          { text: 'hora',value: 'hora'},
          { text: 'estatus',value: 'estatus'}
   ]
    
  }),
  methods: {
    ...mapActions(['doadditemstorage','update']),
    async onsubmit(){

      let id_departamento = this.getuser.departamentos.filter( item => item.nombre == this.selected )[0].id;
      let id = this.getuser.id
      
      let response = await this.doadditemstorage({
        prop: 'salida',
        value: {
          id: id,
          departamento: id_departamento
        }
      })

      if(response){
          this.sheet = false
          this.dialog = true
          this.codigo = response
          this.update()
      }else{
        console.log('error')

      }

      


    }  
  },
  computed: {
    ...mapGetters(['getitems','getuser']) 
  }
}
</script>